body{
  background-color: #242526;
  margin: 0;
  padding: 0;
}

main{
  display: flex;
  align-items: center;
  justify-content: center;
}

img{
  width: 160px;
  height: 160px;
  border-radius: 50%;

}

.wrapper{
  background-color: #3a3b3c;
  width: 450px;
  height: 665px;
  text-align: center;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  color: green;
}

.name > .buttons{
  margin-bottom: 20px;
  background-color: green;
}

.name > .buttons{
  background-color: green;
  width: 80px;
  color: white;
  margin-right: 10px;
  border-radius: 8px;
  border: none;
  padding: 10px;
}

.skills{
  background-color: green;
  width: 80px;
  color: white;
  border: none;
  border-radius: 8px;
  padding: 10px;
}

a {
  padding-right: 12px;
  margin: 10px;

}

.icons{
  padding: 9px;

}

i{
  margin-right: 20px;
  font-size: 22px;
  color: black;
}

button{
  background-color: green;
  border: none;
  width: 80px;
  color: white;
  margin-right: 10px;
  border-radius: 8px;
  border: none;
  padding: 10px;
}

@media(min-width: 450px){
  .wrapper{
margin-top: 100px;
  }
}